.cell {
    position: relative;
    font-size: 20px;
    width: 55px;
    height: 55px;
    cursor: pointer;
    user-select: none;
    font-size: 41px;
}

.highlight {
    position: absolute;
    width: 100%;
    height: 100%;
}

.highlight.empty {
    top: 35%;
    left: 35%;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #0000005f;
}

.highlight.piece {
    box-shadow: inset 0px 0px 4px 3px #9e0101;
}

.highlight.king {
    box-shadow: inset 0px 0px 4px 3px #0019f9;
}

.highlight.passant {
    box-shadow: inset 0px 0px 4px 3px #1cb014;
}

.highlight.underAttack {
    padding: 16px;
    font-size: 14px;
}

.cell.light {
    background: #dbc9b8;
}

.cell.dark {
    background: #51342b;
}

.cell.black-piece {
    color: #000000;
    text-shadow: 1px 1px white;
}

.cell.light-piece {
    color: #ffffff;
    text-shadow: 1px 2px rgb(0, 0, 0);
}

.cell.active {
    color: #7e3ff6;
    text-shadow: 1px 2px #51e5f5;
}